// Modal Dialog
.px2-modal {
	background: rgba(0, 0, 0, 0.7);
	display: flex;
	align-items: center;
	font-family: var(--px2-font-family, $px2-font-family);
	border: none;

	&--background {
		background: transparent;
	}

	&__close {
		position: absolute;
		right: 20px;
		top: 18px;
		width: 20px; height: 20px;
		button {
			position: absolute;
			left: 0;
			right: 0;
			display: block;
			width: 100%; height: 100%;
			padding: 0;
			margin: 0;
			border-radius: 0;
			border: none;
			background-color: transparent;
			color: #999;
			cursor: pointer;
			&::before,
			&::after {
				content: "";
				border-bottom: 1px solid #000;
				width: 115%;
				position: absolute;
				top: 50%;
				left: -9%;
			}
			&::before {
				transform: rotate(45deg);
			}
			&::after {
				transform: rotate(-45deg);
			}
			&:hover {
				&::before,
				&::after {
					border-bottom: 1px solid #666;
				}
			}
			&[data-px2-modal-locked] {
				opacity: 0.2;
			}
		}
	}

	&__dialog {
		display: flex;
		flex-direction: column;
		flex-wrap: nowrap;
		color: $px2-text-color; // fallback
		color: var(--px2-text-color, $px2-text-color);
		background-color: $px2-background-color; // fallback
		background-color: var(--px2-background-color, $px2-background-color);
		box-sizing: border-box;
		padding: 10px 0;
		margin: 20px auto;
		width: calc(100% - 40px);
		max-width: 520px;
		height: auto;
		max-height: calc(100% - 40px);
		overflow: visible;
		border-radius: 5px;
		font-size: medium;
		h1 { font-size: 124%; margin: 0; font-weight: bold; }
		h2 { font-size: 124%; margin: 2em 0 0.5em 0; font-weight: bold; }
		h3 { font-size: 112%; margin: 1em 0 0.5em 0; font-weight: bold; }
		h4 { font-size: 104%; margin: 1em 0 0.5em 0; font-weight: bold; }
		h5 { font-size: 100%; margin: 1em 0 0.5em 0; font-weight: bold; }
		h6 { font-size: 100%; margin: 1em 0 0.5em 0; font-weight: bold; }

		animation: px2-modal--initial .3s ease 0s 1 normal none running;
		transition: transform .3s, opacity .3s;
		&--closed {
			transform: translateY(-10px);
			opacity: 0;
		}
	}

	&__header {
		border-bottom: 1px solid #e5e5e5;
		padding: 8px 40px 8px 20px;
		margin-bottom: 0;
		box-shadow: 0 5px 12px -5px rgba(0, 0, 0, .1);
		z-index: 1100;
	}

	&__body {
		position: relative;
		top: 0; left: 0;
		padding: 0 20px;
		flex-grow: 1;
		box-sizing: border-box;
		z-index: 1050;
	}
	&__body-inner {
		display: block;
		box-sizing: border-box;
	}
	&__body,
	&__body-inner--flex {
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		overflow: auto;
		> * {
			flex-grow: 100;
		}
	}

	&__title {
		font-weight: bold;
		outline: none;
	}

	&__footer {
		border-top: 1px solid #e5e5e5;
		margin: 0 0 0 0;
		padding: 10px 20px 2px 20px;
		display: flex;
		flex-direction: row-reverse;
		justify-content: space-between;
		flex-wrap: nowrap;
		flex-shrink: 0;
		gap: 30px;
		overflow: auto;
		max-height: 30vh;
		z-index: 1100;

		&-primary>ul,
		&-secondary>ul,
		>ul {
			display: flex;
			justify-content: flex-end;
			flex-wrap: nowrap;
			gap: 5px;
			margin: 0;
			padding: 0;
			>li {
				list-style-type: none;
				margin: 0;
				padding: 0;
				white-space: nowrap;
			}
		}
		&-secondary>ul {
			justify-content: flex-start;
		}
	}
}
@keyframes px2-modal--initial {
	0% {
		transform: translateY(-10px);
		opacity: 0;
	}
	100% {
		transform: translateY(0);
		opacity: 1;
	}
}
@keyframes px2-modal--initial-drawer-left {
	0% {
		transform: translateY(0);
		opacity: 1;
		left: -100%;
	}
	100% {
		transform: translateY(0);
		opacity: 1;
		left: 0;
	}
}
@keyframes px2-modal--initial-drawer-right {
	0% {
		transform: translateY(0);
		opacity: 1;
		right: -100%;
	}
	100% {
		transform: translateY(0);
		opacity: 1;
		right: 0;
	}
}

// Drawer (Left/Right)
.px2-modal.px2-modal--type-drawer-left,
.px2-modal.px2-modal--type-drawer-right {
	background: rgba(0, 0, 0, 0.1);

	.px2-modal__dialog {
		position: absolute;
		top: 0;
		left: auto;
		right: auto;
		width: 360px;
		max-width: calc(100% - 20px);
		height: 100%;
		max-height: 100%;
		margin: 0;
		border-radius: 0;
		&--closed {
			transform: translateY(0);
			opacity: 1;
		}
	}
}
.px2-modal.px2-modal--type-drawer-left {
	.px2-modal__dialog {
		left: 0;
		animation: px2-modal--initial-drawer-left .3s ease 0s 1 normal none running;
		transition: transform .3s, opacity .3s, left .3s;
		&--closed {
			left: -100%;
		}
	}
}
.px2-modal.px2-modal--type-drawer-right {
	.px2-modal__dialog {
		right: 0;
		animation: px2-modal--initial-drawer-right .3s ease 0s 1 normal none running;
		transition: transform .3s, opacity .3s, right .3s;
		&--closed {
			right: -100%;
		}
	}
}
